<template>
  <!--  -->
  <div class="register-container">
    <div class="register">
      <iForm :model="loginForm" :rules="loginRule" ref="loginForm">
        <FormItem prop="user">
          <iInput v-model="loginForm.user" type="text" placeholder="" />
        </FormItem>
        <FormItem prop="password">
          <iInput v-model="loginForm.password" type="password" placeholder="" />
        </FormItem>
        <FormItem prop="passwordCheck">
          <iInput v-model="loginForm.passwordCheck" type="password" placeholder="" />
        </FormItem>
        <FormItem prop="validatorCode">
          <iInput style="width: 40%;" v-model="loginForm.validatorCode" type="password" placeholder="" />
          <img />
        </FormItem>
        <FormItem prop="agreementCheck">
          <iCheckbox v-model="loginForm.agreementCheck"><a></a><a></a></iCheckbox>
        </FormItem>
        <FormItem class="less-margin">
          <iButton @click="handleLogin('loginForm')" type="primary" long></iButton>
        </FormItem> 
      </iForm>
    </div>
    <div class="links">
      <a href="/login">>></a>
    </div>
  </div>  
</template>

<script>
  import iForm from 'iview/src/components/form'
  import iInput from 'iview/src/components/input'
  import iButton from 'iview/src/components/button'
  import iCheckbox from 'iview/src/components/checkbox'
  import Message from 'iview/src/components/message'

  export default {
    name: 'login',
    components: {
      iForm,
      FormItem: iForm.Item,
      iInput,
      iButton,
      iCheckbox
    },
    data () {
      // 
      const validatePwd = (rule, value, callback) => {
        if (value.trim() === '') {
          callback(new Error(''))
        } else {
          callback()
        }
        // 
      }
      // 
      const validatePwdCheck = (rule, value, callback) => {
        if (value.trim() === '') {
          callback(new Error(''))
        } else if (value !== this.loginForm.password) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        loginForm: {
          user: '',
          password: '',
          passwordCheck: '',
          validatorCode: ''
        },
        loginRule: {
          user: [
            { required: true, message: '', trigger: 'blur' }
          ],
          password: [
            { validator: validatePwd, trigger: 'blur' }
          ],
          passwordCheck: [
            { validator: validatePwdCheck, trigger: 'blur' }
          ],
          validatorCode: [
            {required: true, message: ''}
          ],
          agreementCheck: [
            {required: true, message: ''}
          ]
        }
      }
    },
    methods: {
      handleLogin (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            console.log(this.loginForm.user)
            this.$http.post('/pub/index/register', this.loginForm).then((response) => {
              // 
              const res = response.data
              if (res.flag) {
                console.log('user_name:::' + res.user_name)
                this.$router.push({path: '/console', query: {name: res.obj.user_name}})
                this.$Message.success('!')
              } else {
                this.$Message.error('!')
              }
            }, (response) => {
              // 
              this.$Message.error('!')
            })
          } else {
            this.$Message.error('!')
          }
        })
      }
    },
    mounted () {
      this.$Message = Message
    }
  }
</script>
<style lang="scss">
  $loginBoxWidth: 280px;
  $logoHeight: 120px;   /* logo: 228*72px */
  $defaultFormItemMargin: 15px;

  .register-container {
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -$loginBoxWidth/2;
    margin-top: -200px;
    width: $loginBoxWidth;
    padding-top: $logoHeight;
    background: url('../assets/logo.png') center 0 no-repeat;
  }
  .less-margin {
    margin-bottom: $defaultFormItemMargin/2;
  }
  .ivu-form-item {
    margin-bottom: $defaultFormItemMargin;
  }
  .ivu-form-item-content .ivu-form-item-error-tip {
    width: 100px;
    top: 20%;
    left: 104%;
  }
</style>



